}
.row {
  --gap: 0px;
  width: calc(100% + var(--gap));
  display: flex;
  flex: 0 1 auto;
  flex-direction: row;
  flex-wrap: wrap;
  box-sizing: border-box;
  overflow: hidden;
  margin: 0 0 calc(-1 * var(--gap)) calc(-1 * var(--gap));
  padding: 0 0 var(--gap) 0;
}

.row-wrapper--ct-wd {
  width: 300px;
  margin: 0 auto;
  overflow: hidden;
}

.row--col-ht > .col > .col__content {
  height: 100%;
}

.row--gap-0 {
  --gap: 0px;
}

.row--gap-1 {
  --gap: 5px;
}

.row--gap-2 {
  --gap: 10px;
}

.row--gap-3 {
  --gap: 20px;
}

.row--xs-top {
  align-items: start;
}

.row--xs-middle {
  align-items: center;
}

.row--xs-bottom {
  align-items: end;
}

.row--xs-start {
  justify-content: start;
}

.row--xs-center {
  justify-content: center;
}

.row--xs-end {
  justify-content: end;
}

.row--xs-rv {
  flex-direction: row-reverse;
}

.col {
  display: block;
  box-sizing: border-box;
  flex: 0 0 auto;
}

.col__content {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  box-sizing: border-box;
  justify-content: start;
}

.col__content > * {
  width: 100%;
  box-sizing: border-box;
}

.col--xs-top > .col__content {
  justify-content: start;
}

.col--xs-middle > .col__content {
  justify-content: center;
}

.col--xs-bottom > .col__content {
  justify-content: end;
}

.col--xs-1 {
  flex-basis: calc(8.3333333333% - var(--gap));
  max-width: calc(8.3333333333% - var(--gap));
  margin: 0 0 var(--gap) var(--gap);
}

.col--xs-2 {
